import {
	Grid,
	Table,
	TableBody,
	TableContainer,
	Alert,
	useTheme,
	Button,
	Stack,
	Divider,
	CardContent,
	CardActions,
} from '@material-ui/core';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { useQuery, gql } from '@apollo/client';
import { useState } from 'react';
import MainCard from '../../../ui-component/cards/MainCard';
import AnimateButton from '../../../ui-component/extended/AnimateButton';
import SubCard from '../../../ui-component/cards/SubCard';
import { gridSpacing } from '../../../store/constant';
import { TOffer } from '../../../types';
import { getOffer } from '../../../graphql/queries';
import Loader from '../../../ui-component/Loader';
import { getOfferPrice } from '../../../helpers/offer';
import TextRow from '../components/TextRow';
import UploadFile, { TFile } from '../components/UploadFile';
import OfferActions from './OfferActions';

type Props = {
	id: string;
};

const OfferEdit = ({ id }: Props) => {
	const { loading, error, data } = useQuery(gql(getOffer), {
		variables: { OfferId: id },
	});
	const offer = _get(data, 'getOffer', {});
	const [signedFiles, setSignedFiles] = useState<TFile[]>([]);
	const [scarFiles, setScarFiles] = useState<TFile[]>([]);
	const theme = useTheme();

	if (loading) return <Loader />;

	if (error && _isEmpty(offer)) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Offer not found
			</Alert>
		);
	}

	const onUploadFile = (setFunc: Function) => (acceptedFiles: TFile[]) => {
		setFunc(acceptedFiles);
	};

	return (
		<MainCard
			title={`#${id}`}
			content={false}
			secondary={<OfferActions offer={offer} />}
		>
			<CardContent>
				<Grid container spacing={gridSpacing}>
					<Grid item lg={6} xs={12}>
						<SubCard title='Offer Information'>
							<TableContainer>
								<Table
									sx={{
										'& td': {
											borderBottom: 'none',
										},
									}}
									size='small'
								>
									<TableBody>
										<TextRow<TOffer> data={offer} source='OfferNo' label='Offer No' />
										<TextRow<TOffer>
											data={offer}
											source='PropertyData.FullAddress'
											label='Property Address'
										/>
										<TextRow<TOffer>
											data={offer}
											source='PropertyData.OwnerNames'
											label='Sellers'
										/>
										<TextRow<TOffer>
											data={offer}
											getValue={getOfferPrice}
											label='Offer Price'
										/>
										<TextRow<TOffer>
											data={offer}
											source='Funding.PurchaseType'
											label='Funding'
										/>
										<TextRow<TOffer>
											data={offer}
											source='Funding.LoanType'
											label='Loan Type'
										/>
										<TextRow<TOffer>
											data={offer}
											source='Funding.DownPayment'
											label='Down Payment'
										/>
										<TextRow<TOffer> data={offer} source='Step.name' label='Status' />
									</TableBody>
								</Table>
							</TableContainer>
						</SubCard>
					</Grid>
					<Grid item lg={6} xs={12}>
						<Grid container direction='column' spacing={gridSpacing}>
							<Grid item xs={12}>
								<SubCard title='Signed Purchase Agreement'>
									<UploadFile
										files={signedFiles}
										onDrop={onUploadFile(setSignedFiles)}
										accept='.pdf'
									/>
								</SubCard>
							</Grid>
							<Grid item xs={12}>
								<SubCard title='SCAR'>
									<UploadFile
										files={scarFiles}
										onDrop={onUploadFile(setScarFiles)}
										accept='.pdf'
									/>
								</SubCard>
							</Grid>
						</Grid>
					</Grid>
				</Grid>
			</CardContent>
			<Divider />
			<CardActions>
				<Grid item xs={12}>
					<Stack direction='row' justifyContent='flex-end'>
						<AnimateButton>
							<Button color='primary' variant='contained' type='submit'>
								Update
							</Button>
						</AnimateButton>
					</Stack>
				</Grid>
			</CardActions>
		</MainCard>
	);
};

export default OfferEdit;
