import { Grid, InputLabel, Button } from '@material-ui/core';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import { makeStyles } from '@material-ui/styles';
import clsx from 'clsx';
import { useDropzone } from 'react-dropzone';
import Preview from './Preview';

const useStyles = makeStyles(() => ({
	fileInput: {
		background: '#fcfcfe',
		width: '100%',
		marginBottom: '12px',
		borderWidth: 2,
		borderRadius: 2,
		borderColor: '#eee',
		borderStyle: 'dashed',
	},
	activeStyle: {
		borderColor: '#2196f3',
	},
	fileContainer: {
		padding: '30px 0',
		textAlign: 'center',
		borderRadius: '4px',
		cursor: 'pointer',
		'& > svg': {
			verticalAlign: 'sub',
			marginRight: '5px',
		},
	},
	uploadImage: {
		position: 'relative',
		overflow: 'hidden',
		borderRadius: '4px',
		cursor: 'pointer',
		width: '100%',
		minHeight: '100px',
		display: 'flex',
		alignItems: 'center',
		justifyContent: 'center',
		'& > svg': {
			verticalAlign: 'sub',
			marginRight: '5px',
		},
		'&:hover $imageHover': {
			opacity: 1,
			zIndex: 1,
		},
	},
	imageHover: {
		position: 'absolute',
		opacity: 0,
		zIndex: -1,
		left: '0',
		top: '0',
		background: 'rgba(255, 255, 255, .8)',
		width: '100%',
		height: '100%',
		display: 'inline-flex',
		transition: 'all 0.3s ease',
	},
	removeButton: {
		margin: 'auto',
	},
}));

export type TFile = File & { preview?: string };

type Props = {
	files: TFile[];
	accept?: string;
	maxFiles?: number;
	onDrop: (files: TFile[]) => void;
};

const UploadFile = (props: Props) => {
	const { files, accept = 'image/*', maxFiles = 1, onDrop } = props;
	const { getRootProps, getInputProps, isDragActive } = useDropzone({
		onDrop,
		accept,
		maxFiles,
	});

	const removeAt = (index: number) => () => {
		const newFiles = [...files];
		newFiles.splice(index, 1);
		onDrop(newFiles);
	};

	const classes = useStyles();

	return (
		<>
			<Button
				{...getRootProps({
					className: clsx(classes.fileInput, {
						[classes.activeStyle]: isDragActive,
					}),
				})}
			>
				<InputLabel className={classes.fileContainer}>
					<CloudUploadIcon />
					Drop file here to upload
				</InputLabel>
			</Button>
			<input {...getInputProps()} />
			<Grid container spacing={1}>
				{files.map((file, index) => (
					<Preview file={file} key={file.name} onRemove={removeAt(index)} />
				))}
			</Grid>
		</>
	);
};

export default UploadFile;
